import { isArray, isObject } from "./utils"
import Danmu from "./Danmu"
export default class VideoDanmu {
    /**
     *
     * @param {object} video videoÁöÑdomÂØπË±°
     * @param {object} canvas canvasÁöÑdomÂØπË±°
     * @param {array} options ÂºπÂπïÊï∞ÊçÆ
     */
    constructor(video, canvas, options) {
        if (!video || !canvas || !options || !isObject(options)) return;
        if (!options.danmuData || !isArray(options.danmuData)) return;
        this.video = video;
        this.canvas = canvas;
        this.canvasCtx = canvas.getContext('2d');
        this.canvas.width = video.offsetWidth;
        this.canvas.height = video.offsetHeight;
        // ÂºπÂπïÊòØÂê¶ÊöÇÂÅú
        this.danmuPaused = true;
        // Â∞ÜÁî®Êà∑‰º†ÂÖ•ÁöÑÊï∞ÊçÆÂíåÈªòËÆ§Êï∞ÊçÆÂêàÂπ∂Âà∞this‰∏ä
        Object.assign(this, options, {
            runTime: 18,
            color: 'red',
            speed: 2,
        })
        this.danmuPool = this.createDanmuPool();
        this.render();
    }

    createDanmuPool() {
        // ÁªôÂºπÂπïÊ∑ªÂä†ÂùêÊ†á
        return this.danmuData.map(dm => new Danmu(dm, this));
    }

    // ÂºπÂπïÊ∏≤Êüì
    render() {
        this.clearRect();
        this.drawDanmu();
        !this.danmuPaused && requestAnimationFrame(this.render.bind(this));
    }

    /**
     * ÁªòÂà∂ÂºπÂπï
     * ÊåÇËΩΩ‰∏§‰∏™‰ø°ÊÅØÔºöstopDrawing ÊòØÂê¶Âá∫Áïå
     *              isInitialized Êï∞ÊçÆÊòØÂê¶ÂàùÂßãÂåñ
     */
    drawDanmu() {
        let currentTime = this.video.currentTime;
        this.danmuPool.map(danmu => {
            // ÈÅøÂÖçÂá∫ÁïåÂºπÂπïÈáçÊñ∞ÁªòÂà∂
            if (!danmu.stopDrawing && currentTime >= danmu.runTime) {
                // ÈÅøÂÖçÈáçÂ§çÂàùÂßãÂåñ
                if (!danmu.isInitialized) {
                    danmu.initialize();
                    danmu.isInitialized = true;
                }
                danmu.x -= danmu.speed;
                danmu.draw();
                if (danmu.x <= danmu.width * -1) {
                    danmu.stopDrawing = true;
                }
            }
        })
    }

    /**
    * ÊãñÂä®ËøõÂ∫¶Êù°ÁöÑÊó∂ÂÄôÈáçÁΩÆÂºπÂπï
    * Âç≥Âà†Èô§‰∏çÂ±û‰∫éÊ≠§Êó∂Èó¥ÊÆµÁöÑÂºπÂπïÂíåÁªòÂà∂Â±û‰∫éÂΩìÂâç‰∫ã‰ª∂ÂùêÊ†áÁöÑÂºπÂπï
    */
    reset() {
        this.clearRect();
        let currentTime = this.video.currentTime;
        console.log(currentTime);
        this.danmuPool.map(danmu => {
            danmu.stopDrawing = false;
            // Á¨¶ÂêàÊó∂Èó¥ËåÉÂõ¥ÁöÑ
            if (currentTime <= danmu.runTime) {
                danmu.isInitialized = false;
                console.log('run');
                // ‰∏çÁ¨¶ÂêàÊó∂Èó¥ËåÉÂõ¥ÁöÑ
            } else {
                console.log('stop');
                danmu.stopDrawing = true;
            }
        })
    }
    /**
     *
     * @param {object} data Ê∑ªÂä†ÁöÑÂºπÂπï‰ø°ÊÅØ
     */
    addDanmu(data) {
        // push Ê∑ªÂä†‰∫ÜÂùêÊ†áÁöÑÁ≠â‰ø°ÊÅØÁöÑÂºπÂπïÊï∞ÊçÆ
        this.danmuPool.push(new Danmu(data, this))
    }

    /**
     * Ê∏ÖÈô§ÁîªÂ∏É
     */
    clearRect() {
        this.canvasCtx.clearRect(0, 0, this.canvas.width, this.canvas.height);
    }
}