function isObject(value) {
    const type = Object.prototype.toString.call(value);
    return type === "[object Object]";
}

function isArray(value) {
    return Array.isArray(value);
}

/**
 *
 * @param {string} content ÊñáÊú¨ÂÜÖÂÆπ
 * @param {number} fontSize Â≠ó‰ΩìÂ§ßÂ∞è
 * @description ÈÄöËøáÂàõÂª∫ÊñáÊú¨ËäÇÁÇπËÆ°ÁÆóÊñáÊú¨ÁöÑÂÆΩÂ∫¶
 */
function getTextWidth(content, fontSize = 30) {
    const _span = document.createElement('span');
    _span.innerText = content;
    _span.style.fontSize = fontSize + 'px';
    _span.style.position = 'absolute';
    document.body.appendChild(_span);
    let width = _span.offsetWidth;
    document.body.removeChild(_span);
    return width;
}

/**
 *
 * @param {object} canvas canvasÂØπË±°
 * @param {number} fontSize
 * @param {object} ctx
 */
function getTextPosition(canvas, fontSize, ctx) {
    let X = canvas.width;
    let Y = canvas.height * Math.random();
    // Èò≤Ê≠¢Ë∂äÁïå
    if (Y < fontSize) Y = fontSize;
    if (Y > canvas.height - fontSize) Y = canvas.height - fontSize;
    ctx.x = X;
    ctx.y = Y;
}

export {
    isArray,
    isObject,
    getTextWidth,
    getTextPosition,
}